.preview {
  width: 30%;
  min-width: 280px;
  background-color: #F7F7F7;
  opacity: 0;
  animation: 0.6s forwards 1.4s opening;
  overflow: hidden;
  display: flex;
  justify-content: center;
  order: 3;
}

@keyframes opening {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
img {
  width: 100%;
}

@media (max-width: 980px) {
  .preview {
    position: relative;
    order: 2;
    min-width: 70%;
    height: 200px;
    overflow: hidden;
  }
  img {
    position: absolute;
    left: 50%;
    min-width: 220px;
    transform: translateX(-50%);
  }
}