* {
    margin: 0;
    padding: 0;
    color: #F7F7F7;
    font-family: 'Rubik';
    }

body{
    background-color: #1D1D20;
}

.container{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    .top_corner{
        position: absolute;
        border-top: 4px solid;
        border-left: 4px solid;
        border-color: #FF6A41;
        width: 30%;
        height: 350px;
        top: 50%;
        left: 50%;
        z-index: -1;
        transition: all 0.2s ease-in;
        animation: forwards 1s openingTop;
        }
    .bottom_corner{
        position: absolute;
        border-bottom: 4px solid;
        border-right: 4px solid;
        border-color: #FF6A41;
        width: 30%;
        height: 350px;
        bottom: 50%;
        right: 50%;
        z-index: -1;   
        transition: all 0.2s ease-in;
        animation: forwards 1s openingBottom;
    }
}
.main_container {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}        
main{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

@keyframes openingTop {
    from{
        top: 50%;
        left: 50%;
        width: 1%;
        height: 1px;
        border-color: #F7F7F7;
    }
    to{
        top: 10%;
        left: 10%;
        width: 30%;
        height: 350px;
        border-color: #FF6A41;
    }
}
@keyframes openingBottom {
    from{
        bottom: 50%;
        right: 50%;
        width: 1%;
        height: 1px;
        border-color: #F7F7F7;
    }
    to{
        bottom: 10%;
        right: 10%;
        width: 30%;
        height: 350px;
        border-color: #FF6A41;
    }
}

@media(max-width:980px){
    .main_container {
        margin-top: 100px;
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }
    main{
        flex-direction: column;
        justify-content: space-evenly;     
        margin: 0;  
        height: 70vh; 
    }
    .container{
        height: auto;
    }
}