footer {
  font-size: 12px;
  display: flex;
  flex-direction: row-reverse;
  opacity: 0;
  animation: 0.6s forwards 1.6s opening;
}

@keyframes opening {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
h3 {
  position: relative;
}

h3::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  border-radius: 4px;
  background-color: #FF6A41;
  bottom: -3px;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
  cursor: default;
}

h3:hover {
  cursor: default;
}

h3:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}